import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { update_profile } from '../actions/profile'
import axios from 'axios'
import { delete_account } from '../actions/auth'
import { ToastContainer } from 'react-toastify'
import '../../node_modules/react-toastify/dist/ReactToastify.css'

function Profile({
  delete_account,
  update_profile,
  first_name_global,
  last_name_global,
  role_global,
  darjah_global,
  kelas_global,
  user_global
}) {
  const [formData, setFormData] = useState({
    first_name: '',
    last_name: '',
    role: '',
    darjah: '',
    kelas: '',
  })


  const { first_name, last_name, role, darjah, kelas } = formData
  const [roleOri, setRoleOri] = useState([])
  const [darjahOri, setDarjahOri] = useState([])
  const [kelasOri, setKelasOri] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    setFormData({
      first_name: first_name_global,
      last_name: last_name_global,
      role: role_global,
      darjah: darjah_global,
      kelas: kelas_global,
    })
  }, [first_name_global])

  useEffect(async () => {
    await axios.get(`${process.env.REACT_APP_API_URL}/accounts/role/`)
      .then((response) => {
        setRoleOri(response.data)
        setIsLoading(false)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(async () => {
    await axios.get(`${process.env.REACT_APP_API_URL}/accounts/darjah/`)
      .then((response) => {
        setDarjahOri(response.data)
        setIsLoading(false)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(async () => {
    await axios.get(`${process.env.REACT_APP_API_URL}/accounts/kelas/`)
      .then((response) => {
        setKelasOri(response.data)
        setIsLoading(false)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmit = e => {
    e.preventDefault();
    update_profile(first_name, last_name, role, darjah, kelas)
  }

  //  const test2 = () => {
  //    if (role.indexOf(',') > -1) {
  //      const test2 = role.split(',')
  //    }
  //  }

  const kelasTest = kelas.includes(",")
  const darjahTest = darjah.includes(",")

  console.log(kelasTest)
  console.log(darjahTest)

  return (
    <div>
      <div className='container-fluid container-content' id="content">
        <h1 className='mt-3 mb-3'>Selamat Datang : {first_name_global} {last_name_global}</h1>
        <p className='mt-3 mb-3'>Kemas kini profil pengguna anda di bawah:</p>
        <form onSubmit={e => onSubmit(e)}>
          <div className="form-group">
            <label className='form-label mt-3' htmlFor='first_name'>Nama Pertama</label>
            <input className='form-control' type='text' name='first_name' onChange={e => onChange(e)} value={first_name} placeholder={`${first_name_global}`} />
            <label className='form-label mt-3' htmlFor='last_name'>Nama Akhir</label>
            <input className='form-control' type='text' name='last_name' onChange={e => onChange(e)} value={last_name} placeholder={`${last_name_global}`} />
            <label className='form-label mt-3' htmlFor='role'>Peranan</label>
            {
              roleOri.filter(x => x.key === role).map((x) => {
                return (
                  <input className='form-control' type='text' name='role' value={x.value} disabled="disabled" isLoading={isLoading} />
                )
              })
            }
            <label className='form-label mt-3' htmlFor='role'>Darjah</label>
            {
              darjahTest === true ? (
                darjahOri.filter(x => x.key >= darjah.split(",")[0] && x.key <= darjah.split(",")[darjah.split(",").length - 1]).map((x) => {
                  return(
                    <input className='form-control' type='text' name='role' value={x.value} disabled="disabled" isLoading={isLoading} />
                  )
                })
              ): (
                darjahOri.filter(x => x.key === darjah).map((x) => {
                  return (
                    <input className='form-control' type='text' name='role' value={x.value} disabled="disabled" isLoading={isLoading} />
                  )
                })
              )
            }
            <label className='form-label mt-3' htmlFor='role'>Kelas</label>
            {
              kelasTest === true ? (
                kelasOri.filter(x => x.key >= kelas.split(",")[0] && x.key <= kelas.split(",")[kelas.split(",").length - 1]).map((x) => {
                  return(
                    <input className='form-control' type='text' name='role' value={x.value} disabled="disabled" isLoading={isLoading} />
                  )
                })
              ): (
                kelasOri.filter(x => x.key === kelas).map((x) => {
                  return (
                    <input className='form-control' type='text' name='role' value={x.value} disabled="disabled" isLoading={isLoading} />
                  )
                })
              )
            }
          </div>
          <button className="btn btn-primary mt-3" type='submit'>Kemas kini Profil</button>
        </form>
        {/* <p className='mt-5'>
          Click the button below to delete your account
        </p>
        <a href="#!" className="btn btn-danger" onClick={delete_account}>Delete Account</a> */}
      </div>
      <ToastContainer
        position="top-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </div>
  )
}

const mapStateToProps = state => ({
  first_name_global: state.profile.first_name,
  last_name_global: state.profile.last_name,
  role_global: state.profile.role,
  darjah_global: state.profile.darjah,
  kelas_global: state.profile.kelas,
  user_global: state.profile.user,
})

export default connect(mapStateToProps, {
  update_profile,
  delete_account
})(Profile)