import React, { Fragment, Component, useState } from 'react';
import { Link, NavLink } from 'react-router-dom'
import { connect } from 'react-redux'
import { logout } from '../actions/auth'
import * as AiIcons from "react-icons/ai";
import { FaChevronRight } from 'react-icons/fa';
import { FaChartPie } from "react-icons/fa";
import { MdOutlineInput } from "react-icons/md"
import { MdManageAccounts } from "react-icons/md"
import { RiBarChartBoxFill } from "react-icons/ri"
import { IoPeopleSharp, IoLogOut } from "react-icons/io5"
import { BsClipboardData } from "react-icons/bs";
import { GrDocumentDownload } from "react-icons/gr";
import { IoMdClose } from "react-icons/io";
import { BsArrowLeftSquare } from "react-icons/bs"

function Navbar({ isAuthenticated, isRole, logout }) {
  const [show, setShow] = useState(true)

  const showMenu = () => {
    setShow(false)
  }

  const authLinksTeacher = (
    <div id='sidebar' className={show === true ? "": "hide"}>
      {/* <div className='hamb-menu'>
        <BsArrowLeftSquare className='hamb-icon' onClick={showMenu} />
      </div> */}
      {
        show === true ? (
          <div>
            <div className='sidebar-header'>
              <h1 className='Nevbar-title'>eNILAM</h1>
              {/* <h4>{isRole}</h4> */}
              {
                isRole === "Teacher" ? (
                  <h4>Guru</h4>
                ): null
              }
            </div>
            <NavLink className="nav-link" to="/profile_teacher">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Profil</span>
            </NavLink>
            <NavLink className="nav-link" to="/booklist_teacher">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Senarai Buku</span>
            </NavLink>
            <NavLink className="nav-link" to="/ranking_teacher">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Rank</span>
            </NavLink>
            <NavLink className="nav-link" to="/report">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Laporan</span>
            </NavLink>
            <div className='divider-navbar'></div>
            <div className="nav-link logout">
              <IoLogOut /><a className="nav-link-drop menu-text" onClick={logout} href='#!'>Log keluar</a>
            </div>
          </div>
        ) : (
          <div>
            <div className='sidebar-header'>
              <h3 style="color:blue;">eNILAM</h3>
            </div>
            <NavLink className="nav-link" to="/profile">
              <AiIcons.AiFillProfile className='icondrop' />
            </NavLink>
            <div className='divider-navbar'></div>
            <div className="nav-link logout">
              <IoLogOut /><a className="nav-link-drop menu-text" onClick={logout} href='#!'>Log Keluar</a>
            </div>
          </div>
        )
      }
    </div>
  )

  const authLinksStudent = (
    <div id='sidebar' className={show === true ? "": "hide"}>
      {/* <div className='hamb-menu'>
        <BsArrowLeftSquare className='hamb-icon' onClick={showMenu} />
      </div> */}
      {
        show === true ? (
          <div>
            <div className='sidebar-header'>
              <h1 className='Nevbar-title'>eNILAM</h1>
              {/* <h4>{isRole}</h4> */}
              {
                isRole === "Student" ? (
                  <h4>Pelajar</h4>
                ): null
              }
            </div>
            <NavLink className="nav-link" to="/profile">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Profil</span>
            </NavLink>
            <NavLink className="nav-link" to="/booklist">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Senarai Buku</span>
            </NavLink>
            <NavLink className="nav-link" to="/ranking">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Rank</span>
            </NavLink>
            <NavLink className="nav-link" to="/book_suggestion">
              <AiIcons.AiFillProfile className='icondrop' /><span className='menu-text submenudrop'>Cadangan Buku</span>
            </NavLink>
            <div className='divider-navbar'></div>
            <div className="nav-link logout">
              <IoLogOut /><a className="nav-link-drop menu-text" onClick={logout} href='#!'>Log Keluar</a>
            </div>
          </div>
        ) : (
          <div>
            <div className='sidebar-header'>
              <h3>eNILAM</h3>
            </div>
            <NavLink className="nav-link" to="/profile">
              <AiIcons.AiFillProfile className='icondrop' />
            </NavLink>
            <NavLink className="nav-link" to="/booklist">
              <AiIcons.AiFillProfile className='icondrop' />
            </NavLink>
            <div className='divider-navbar'></div>
            <div className="nav-link logout">
              <IoLogOut /><a className="nav-link-drop menu-text" onClick={logout} href='#!'>Log Keluar</a>
            </div>
          </div>
        )
      }
    </div>
  )

  return (
    <div className='container-fluid content-sidebar'>
      {isAuthenticated && isRole === "Student" ? authLinksStudent : isAuthenticated && isRole === "Teacher" ? authLinksTeacher : null}
    </div>
  )
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  isRole: state.profile.role
})

export default connect(mapStateToProps, { logout })(Navbar)
