import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Navigate, Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { register } from '../actions/auth'
import CSRFToken from '../components/CSRFToken'
import Select from 'react-select'

function Register({ register, isAuthenticated }) {

  const [formData, setFormData] = useState({
    email: '',
    password: '',
    re_password: '',
    first_name: '',
    last_name: '',
  })

  const [accountCreated, setAccountCreated] = useState(false)
  const [role, setRole] = useState([])
  const [darjah, setDarjah] = useState([])
  const [kelas, setKelas] = useState([])


  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/role/`)
      .then((response) => {
        setRole(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/darjah/`)
      .then((response) => {
        setDarjah(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/kelas/`)
      .then((response) => {
        setKelas(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  const { email, password, re_password, first_name, last_name } = formData

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

  const [roleSelected, setRoleSelected] = useState('')
  const [darjahSelected, setDarjahSelected] = useState('')
  const [kelasSelected, setKelasSelected] = useState('')
  const [userinfo, setUserInfo] = useState({
    darjahChecked: [],
    response: [],
  });
  const [userInfoKelas, setUserInfoKelas] = useState({
    kelasChecked: [],
    response: [],
  });

  function handleRoleChange(event) {
    setRoleSelected(event.target.value)
  }

  function handleDarjahChange(event) {
    setDarjahSelected(event.target.value)
  }

  function handleKelasChange(event) {
    setKelasSelected(event.target.value)
  }

  const handleChangeDarjahChecked = (e) => {
    // Destructuring
    const { value, checked } = e.target;
    const { darjahChecked } = userinfo;

    // Case 1 : The user checks the box
    if (checked) {
      setUserInfo({
        darjahChecked: [...darjahChecked, value],
        response: [...darjahChecked, value],
      });
    }

    // Case 2  : The user unchecks the box
    else {
      setUserInfo({
        darjahChecked: darjahChecked.filter((e) => e !== value),
        response: darjahChecked.filter((e) => e !== value),
      });
    }
  };

  const handleChangeKelasChecked = (e) => {
    // Destructuring
    const { value, checked } = e.target;
    const { kelasChecked } = userInfoKelas;

    // Case 1 : The user checks the box
    if (checked) {
      setUserInfoKelas({
        kelasChecked: [...kelasChecked, value],
        response: [...kelasChecked, value],
      });
    }

    // Case 2  : The user unchecks the box
    else {
      setUserInfoKelas({
        kelasChecked: kelasChecked.filter((e) => e !== value),
        response: kelasChecked.filter((e) => e !== value),
      });
    }
  };

  console.log(userinfo.darjahChecked)
  console.log(userInfoKelas.kelasChecked)
  console.log(roleSelected)

  const onSubmit = e => {
    e.preventDefault();

    if (password === re_password) {
      if(roleSelected === "Teacher"){
        register(email, password, re_password, first_name, last_name, roleSelected, userinfo.darjahChecked, userInfoKelas.kelasChecked);
      }else{
        register(email, password, re_password, first_name, last_name, roleSelected, darjahSelected, kelasSelected);
      }
        
      setAccountCreated(true)
    }
  }

  if (isAuthenticated)
    return <Navigate to="/dashboard" />
  else if (accountCreated)
    return <Navigate to="/" />


  return (
    <div className='container-fluid register-main'>
      <div className='container-fluid register-container'>
        <h1>Daftar untuk Akaun</h1>
        <h2 className='register-title'>eNilam</h2>
        <p>Buat akaun baharu</p>
        <form onSubmit={e => onSubmit(e)}>
          <CSRFToken />
          <div className='form-group'>
            <label className='form-label mt-3'>Emel:</label>
            <input className='form-control login-form' type='email' placeholder='Emel*' name='email' onChange={e => onChange(e)} value={email} required />
          </div>
          <div className='form-group'>
            <label className='form-label mt-3'>Kata Laluan:</label>
            <input className='form-control login-form' type='password' placeholder='Kata laluan*' name='password' onChange={e => onChange(e)} value={password} minLength='6' required />
          </div>
          <div className='form-group'>
            <label className='form-label mt-3'>Sahkan Kata Laluan:</label>
            <input className='form-control login-form' type='password' placeholder='Sahkan Kata Laluan*' name='re_password' onChange={e => onChange(e)} value={re_password} minLength='6' required />
          </div>
          <div className='d-flex justify-content-between'>
            <div className='form-group w-100'>
              <label className='form-label mt-3'>Nama Pertama:</label>
              <input className='form-control login-form register-form-name' type='text' placeholder='Nama Pertama*' name='first_name' onChange={e => onChange(e)} value={first_name} required />
            </div>
            <div className='form-group w-100'>
              <label className='form-label mt-3'>Nama Akhir:</label>
              <input className='form-control login-form register-form-name' type='text' placeholder='Nama Akhir*' name='last_name' onChange={e => onChange(e)} value={last_name} required />
            </div>
          </div>
          <div className='form-group'>
            <label className='form-label mt-3'>Peranan:</label>
            <select className="form-control login-form" value={roleSelected} onChange={handleRoleChange} required>
              <option className='option-text' value="">Pilih...</option>
              {
                role.map((x) => {
                  return (
                    <option className='option-text' value={x.key} onChange={e => onChange(e)}>{x.value}</option>
                  )
                })
              }
            </select>
          </div>

          {
            roleSelected === "Teacher" ? (
              <div className='form-group'>
                <label className='form-label mt-3'>Darjah:</label>
                {
                  darjah.map((x) => {
                    return (
                      <div className="form-check m-3">
                        <input type="checkbox" name="darjah" value={x.key} onChange={handleChangeDarjahChecked} />
                        <label htmlFor="">{x.value}</label>
                      </div>
                    )
                  })
                }
              </div>
            ) : (
              <div className='form-group'>
                <label className='form-label mt-3'>Darjah:</label>
                <select className="form-control login-form" value={darjahSelected} onChange={handleDarjahChange} required>
                  <option className='option-text' value="">Pilih...</option>
                  {
                    darjah.map((x) => {
                      return (
                        <option className='option-text' value={x.key}>{x.value}</option>
                      )
                    })
                  }
                </select>
              </div>
            )
          }

          {
            roleSelected === "Teacher" ? (
              <div className='form-group'>
                <label className='form-label mt-3'>Kelas:</label>
                {
                  kelas.map((x) => {
                    return (
                      <div className="form-check m-3">
                        <input type="checkbox" name="kelas" value={x.key} onChange={handleChangeKelasChecked} />
                        <label htmlFor="">{x.value}</label>
                      </div>
                    )
                  })
                }
              </div>
            ) : (
              <div className='form-group'>
                <label className='form-label mt-3'>Kelas:</label>
                <select className="form-control login-form" value={kelasSelected} onChange={handleKelasChange} required>
                  <option className='option-text' value="">Pilih...</option>
                  {
                    kelas.map((x) => {
                      return (
                        <option className='option-text' value={x.key}>{x.value}</option>
                      )
                    })
                  }
                </select>
              </div>
            )
          }
          <button className='btn btn-primary mt-3 mb-3' type='submit'>Daftar</button>
        </form>
        <p>Sudah mempunyai akaun? <Link to='/' className='login-LoginPage'>Log Masuk</Link></p>
      </div >
    </div >
  )
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps, { register })(Register)