import React, { useState, useEffect } from 'react'
import axios from 'axios'
import moment from 'moment';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Cookies from 'js-cookie'

function StudentsRanking() {

    const todayDate = new Date()

    const [ranking, setRanking] = useState([])
    const [rankingV2, setRankingV2] = useState([])
    const [user, setUser] = useState([])
    const [searchDate, setSearchDate] = useState(todayDate)
    const [filterBy, setFilterBy] = useState('Semua')
    const [bookList, setBookList] = useState([])

    useEffect(async () => {
        axios.get(`${process.env.REACT_APP_API_URL}/books/students_ranking/`)
            .then((response) => {
                setRanking(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(async () => {
        axios.get(`${process.env.REACT_APP_API_URL}/accounts/get_users/`)
            .then((response) => {
                setUser(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(async () => {
        axios.get(`${process.env.REACT_APP_API_URL}/books/book/`)
            .then((response) => {
                setBookList(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])


    const getMonth = moment(searchDate).month() + 1
    const getYear = moment(searchDate).year()

    const rankingFiltered = (getMonth, getYear) => {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-CSRFToken': Cookies.get('csrftoken')
            }
        }

        const body = JSON.stringify({ getMonth, getYear })

        axios.post(`${process.env.REACT_APP_API_URL}/books/students_ranking/`, body, config)
            .then((response) => {
                setRankingV2(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }

    function handleFilterBy(event) {
        setFilterBy(event.target.value)
    }

    console.log(filterBy)

    const books_quantity_all = ranking.sort((a, b) => b.book_count - a.book_count)
    const firstnamefirst_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[0]).map(data => data.first_name)
    const firstnamesecond_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[1]).map(data => data.first_name)
    const firstnamethird_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[2]).map(data => data.first_name)
    const lastnamefirst_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[0]).map(data => data.last_name)
    const lastnamesecond_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[1]).map(data => data.last_name)
    const lastnamethird_all = user.filter(data => data.id === books_quantity_all.map(x => x.created_by)[2]).map(data => data.last_name)

    const books_quantity = rankingV2.sort((a, b) => b.book_count - a.book_count)
    const firstnamefirst = user.filter(data => data.id === books_quantity.map(x => x.created_by)[0]).map(data => data.first_name)
    const firstnamesecond = user.filter(data => data.id === books_quantity.map(x => x.created_by)[1]).map(data => data.first_name)
    const firstnamethird = user.filter(data => data.id === books_quantity.map(x => x.created_by)[2]).map(data => data.first_name)
    const lastnamefirst = user.filter(data => data.id === books_quantity.map(x => x.created_by)[0]).map(data => data.last_name)
    const lastnamesecond = user.filter(data => data.id === books_quantity.map(x => x.created_by)[1]).map(data => data.last_name)
    const lastnamethird = user.filter(data => data.id === books_quantity.map(x => x.created_by)[2]).map(data => data.last_name)

    return (
        <div className='container-fluid container-content'>
            <h1 className='text-center input-Title'>Ranking</h1>
            <div className='my-5 row justify-content-center'>
                <div className='col-3'>
                    <div className='row justify-content-center'>
                        <div className='col d-flex justify-content-around'>
                            <div className='row'>
                                <div className='col'>
                                    <label htmlFor="" className='form-label'>Pilih jenis saringan</label>
                                    <select className="form-control" value={filterBy} onChange={handleFilterBy}>
                                        <option value="Semua">Semua</option>
                                        <option value="Bulan">Bulan Dan Tahun</option>
                                    </select>
                                </div>

                                {
                                    filterBy === 'Semua' ? null : (
                                        <div className='col'>
                                            <div>
                                                <label className='form-label' htmlFor='month_year'>Bulan/Tahun: </label>
                                                <div className='d-flex justify-content-between'>
                                                    <div>
                                                        <DatePicker
                                                            selected={searchDate}
                                                            onChange={setSearchDate}
                                                            dateFormat="MM/yyyy"
                                                            showMonthYearPicker
                                                            showFullMonthYearPicker
                                                            className="form-control"
                                                        />
                                                    </div>
                                                    <div>
                                                        <button className='btn btn-primary' onClick={(e) => rankingFiltered(getMonth, getYear)}>Search</button>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                    </div>

                </div>
            </div>
            {/* {
                filterBy === 'Semua' ? null : (
                    <div className='d-flex justify-content-center my-5'>
                        <div className='col-4'>
                            <label className='form-label' htmlFor='month_year'>Bulan/Tahun: </label>
                            <div className='d-flex justify-content-between'>
                                <div>
                                    <DatePicker
                                        selected={searchDate}
                                        onChange={setSearchDate}
                                        dateFormat="MM/yyyy"
                                        showMonthYearPicker
                                        showFullMonthYearPicker
                                        className="form-control"
                                    />
                                </div>
                                <div>
                                    <button className='btn btn-primary' onClick={(e) => rankingFiltered(getMonth, getYear)}>Search</button>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            } */}

            {
                filterBy === 'Semua' ? (
                    <>
                        <div className='upper-top pt-5'>
                            <div className='rank-first text-center'>
                                <a href="https://imgbb.com/"><img src="https://i.ibb.co/0y6PYyk/crown.png" alt="crown" border="0" className='crown-logo' /></a>
                                <h3>1. {firstnamefirst_all} {lastnamefirst_all}</h3>
                                <h3>{books_quantity_all.filter(data => data.created_by === books_quantity_all.map(x => x.created_by)[0]).map(x => x.book_count)} Buah Buku</h3>
                            </div>
                            <div className='rank-sec-th-main row text-center d-flex justify-content-around'>
                                <div className='rank-sec-th text-center'>
                                    <h4>2. {firstnamesecond_all} {lastnamesecond_all}</h4>
                                    <h4>{books_quantity_all.filter(data => data.created_by === books_quantity_all.map(x => x.created_by)[1]).map(x => x.book_count)} Buah Buku</h4>
                                </div>
                                <div className='rank-sec-th text-center'>
                                    <h4>3. {firstnamethird_all} {lastnamethird_all}</h4>
                                    <h4>{books_quantity_all.filter(data => data.created_by === books_quantity_all.map(x => x.created_by)[2]).map(x => x.book_count)} Buah Buku</h4>
                                </div>
                            </div>
                        </div>
                        <div className='others pt-5 pb-3'>
                            {
                                books_quantity_all.map((x, index) => {
                                    if (index > 2) {
                                        return (
                                            <div className='rank-other text-center d-flex justify-content-around'>
                                                <p>{(index) + 1}.</p>
                                                <p>{user.filter(data => data.id === x.created_by).map(data => data.first_name)}</p>
                                                <p>{x.book_count}</p>
                                            </div>
                                        )
                                    }
                                })
                            }

                        </div>
                    </>

                ) : (
                    <>
                        <div className='upper-top pt-5'>
                            <div className='rank-first text-center'>
                                <a href="https://imgbb.com/"><img src="https://i.ibb.co/0y6PYyk/crown.png" alt="crown" border="0" className='crown-logo' /></a>
                                <h3>1. {firstnamefirst} {lastnamefirst}</h3>
                                <h3>{books_quantity.filter(data => data.created_by === books_quantity.map(x => x.created_by)[0]).map(x => x.book_count)} Buah Buku</h3>
                            </div >
                            <div className='rank-sec-th-main row text-center d-flex justify-content-around'>
                                <div className='rank-sec-th text-center'>
                                    <h4>2. {firstnamesecond} {lastnamesecond}</h4>
                                    <h4>{books_quantity.filter(data => data.created_by === books_quantity.map(x => x.created_by)[1]).map(x => x.book_count)} Buah Buku</h4>
                                </div>
                                <div className='rank-sec-th text-center'>
                                    <h4>3. {firstnamethird} {lastnamethird}</h4>
                                    <h4>{books_quantity.filter(data => data.created_by === books_quantity.map(x => x.created_by)[2]).map(x => x.book_count)} Buah Buku</h4>
                                </div>
                            </div>
                        </div >
                        <div className='others pt-5 pb-3'>
                            {
                                books_quantity.map((x, index) => {
                                    if (index > 2) {
                                        return (
                                            <div className='rank-other text-center d-flex justify-content-around'>
                                                <p>{(index) + 1}.</p>
                                                <p>{user.filter(data => data.id === x.created_by).map(data => data.first_name)}</p>
                                                <p>{x.book_count}</p>
                                            </div>
                                        )
                                    }
                                })
                            }

                        </div>
                    </>
                )
            }


        </div >
    )
}

export default StudentsRanking