import React, { useState, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { input_book } from '../actions/books'
import axios from 'axios'
import { connect } from 'react-redux'
import { ToastContainer } from 'react-toastify'

function BookInput({
    input_book
}) {

    const [lang, setLang] = useState([])
    const [tag, setTag] = useState([])

    const [title, setTitle] = useState('')
    const [author, setAuthor] = useState('')
    const [datePublished, setDatePublished] = useState('')
    const [dateRead, setDateRead] = useState('')
    const [page, setPage] = useState('')
    const [selectedLang, setSelectedLang] = useState('Choose...')
    const [selectedTag, setSelectedTag] = useState('Choose...')
    const [summary, setSummary] = useState('')

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/books/lang/`)
            .then((response) => {
                setLang(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/books/tag/`)
            .then((response) => {
                setTag(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    function handleTitleChange(event) {
        setTitle(event.target.value)
    }

    function handleAuthorChange(event) {
        setAuthor(event.target.value)
    }

    function handleDatePublishedChange(event) {
        setDatePublished(event.target.value)
    }

    function handleDateReadChange(event) {
        setDateRead(event.target.value)
    }

    function handlePageChange(event) {
        setPage(event.target.value)
    }

    function handleLangChange(event) {
        setSelectedLang(event.target.value)
    }

    function handleTagChange(event) {
        setSelectedTag(event.target.value)
    }

    function handleSummaryChange(event) {
        setSummary(event.target.value)
    }

    let navigate = useNavigate()

    const onSubmit = e => {
        e.preventDefault();
        input_book(title, author, datePublished, dateRead, page, selectedLang, selectedTag, summary)
        setTitle('')
        setAuthor('')
        setDatePublished('')
        setDateRead('')
        setPage('')
        setSelectedLang('')
        setSelectedTag('')
        setSummary('')
    }

    const cancel = e => {
        e.preventDefault();
        navigate('/booklist')
    }


    return (
        <div className='container-fluid container-content'>
            <div className='page-title'>
                <h1 className='input-Title'>Tambah Rekod</h1>
            </div>
            <form onSubmit={e => onSubmit(e)}>
                <div className="form-group">
                    <label className='form-label mt-3'>Tajuk:</label>
                    <input type="text" className="form-control" placeholder="Masukkan Tajuk" name="title" value={title} onChange={handleTitleChange} />
                </div>
                <div className="form-group">
                    <label className='form-label mt-3'>Pengarang:</label>
                    <input type="text" className="form-control" placeholder="Masukkan Pengarang" name="title" value={author} onChange={handleAuthorChange} />
                </div>
                <div className="form-group">
                    <label className='form-label mt-3' htmlFor='date'>Tarikh Diterbitkan:</label>
                    <input className="form-control" type="date" name="date" placeholder='Pilih Tarikh' value={datePublished} onChange={handleDatePublishedChange} />
                </div>
                <div className="form-group">
                    <label className='form-label mt-3' htmlFor='date'>Tarikh Dibaca:</label>
                    <input className="form-control" type="date" name="date" placeholder='Pilih Tarikh' value={dateRead} onChange={handleDateReadChange} />
                </div>
                <div className="form-group">
                    <label className='form-label mt-3'>Bil. Mukasurat:</label>
                    <input type="number" className="form-control" placeholder="Masukkan Bilangan" name="title" value={page} onChange={handlePageChange} />
                </div>
                <div className="form-group">
                    <label className='form-label mt-3' htmlFor='estate'>Bahasa:</label>
                    <select className="form-control" value={selectedLang} onChange={handleLangChange}>
                        <option value="">Pilih...</option>
                        {
                            lang.map((x) => {
                                return (
                                    <option value={x.key}>{x.value}</option>
                                )
                            })
                        }
                    </select>
                </div>
                <div className="form-group">
                    <label className='form-label mt-3' htmlFor='estate'>Tag:</label>
                    <select className="form-control" value={selectedTag} onChange={handleTagChange}>
                        <option value="">pilih...</option>
                        {
                            tag.map((x) => {
                                return (
                                    <option value={x.key}>{x.value}</option>
                                )
                            })
                        }
                    </select>
                </div>
                <div className="form-group">
                    <label className='form-label mt-3' htmlFor='date'>Ringkasan:</label>
                    <textarea className="form-control" type="textarea" name="summary" placeholder='Ringkasan...' value={summary} onChange={handleSummaryChange} />
                </div>
                <div className='end-button d-flex justify-content-between'>
                    <button className="btn btn-primary btn-block mt-3 update-btn" type='submit'>Tambah</button>
                    <div>
                        <button className="btn btn-dark btn-block mt-3 mx-3 cancel-btn" onClick={e => cancel(e)}>Batal</button>
                    </div>
                </div>
            </form>
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />

        </div>
    )
}

const mapStateToProps = state => ({
    first_name_global: state.profile.first_name,
    last_name_global: state.profile.last_name,
    role_global: state.profile.role,
    darjah_global: state.profile.darjah,
    kelas_global: state.profile.kelas,
    user_global: state.profile.user,
})

export default connect(mapStateToProps, { input_book, })(BookInput)