import {
    LOAD_USER_PROFLE_SUCCESS,
    LOAD_USER_PROFLE_FAIL,
    UPDATE_USER_PROFLE_SUCCESS,
    UPDATE_USER_PROFLE_FAIL
} from '../actions/types'

const initialState = {
    username: '',
    first_name: '',
    last_name: '',
    role: '',
    darjah: '',
    kelas: '',
    user: '',
}

export default function (state = initialState, action) {
    const { type, payload } = action

    switch (type) {
        case LOAD_USER_PROFLE_SUCCESS:
        case UPDATE_USER_PROFLE_SUCCESS:
            return {
                ...state,
                username: payload.username,
                first_name: payload.profile.first_name,
                last_name: payload.profile.last_name,
                role: payload.profile.role,
                darjah: payload.profile.darjah,
                kelas: payload.profile.kelas,
                user: payload.profile.user
            }
        case LOAD_USER_PROFLE_FAIL:
            return {
                ...state,
                username: '',
                first_name: '',
                last_name: '',
                role: '',
                darjah: '',
                kelas: '',
                user: '',
            }

        case UPDATE_USER_PROFLE_FAIL:
            return {
                ...state
            }
        default:
            return state
    }
}

