import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Layout from './hocs/Layout'
import Register from './containers/Register'
import Login from './containers/Login'
import Profile from './containers/Profile'
import PrivateRoute from './hocs/PrivateRoute'
import PrivateRouteTeacher from './hocs/PrivateRouteTeacher';
import BookList from './containers/BookList';
import BookListTeacher from './containers/BookListTeacher';
import BookUpdate from './containers/BookUpdate';
import BookUpdateTeacher from './containers/BookUpdateTeacher';
import BookInput from './containers/BookInput';
import StudentsRanking from './containers/StudentsRanking';
import Report from './containers/Report';
import BookSuggestion from './containers/BookSuggestion';
import { connect } from 'react-redux'

import { Provider } from 'react-redux'
import store from './store'
import "./static/navbar.css"
import "./static/main.css"
import { Navigate } from 'react-router-dom';


const App = () => (
    <Provider store={store}>
        <Router>
            <Layout />
            <Routes>
                <Route path="/register" element={<Register />} />
                <Route path="/" element={<Login />} />

                <Route path="/profile" element={
                    <PrivateRoute>
                        <Profile />
                    </PrivateRoute>
                } />

                <Route path="/profile_teacher" element={
                    <PrivateRouteTeacher>
                        <Profile />
                    </PrivateRouteTeacher>
                } />

                <Route path="/booklist" element={
                    <PrivateRoute>
                        <BookList />
                    </PrivateRoute>
                } />

                <Route path="/booklist_teacher" element={
                    <PrivateRouteTeacher>
                        <BookListTeacher />
                    </PrivateRouteTeacher>
                } />

                <Route path="/input_book/" element={
                    <PrivateRoute>
                        <BookInput />
                    </PrivateRoute>
                } />

                <Route path="/update_book/:uid" element={
                    <PrivateRoute>
                        <BookUpdate />
                    </PrivateRoute>
                } />

                <Route path="/update_book_teacher/:uid" element={
                    <PrivateRouteTeacher>
                        <BookUpdateTeacher />
                    </PrivateRouteTeacher>
                } />

                <Route path="/ranking" element={
                    <PrivateRoute>
                        <StudentsRanking />
                    </PrivateRoute>
                } />

                <Route path="/ranking_teacher" element={
                    <PrivateRouteTeacher>
                        <StudentsRanking />
                    </PrivateRouteTeacher>
                } />

                <Route path="/report" element={
                    <PrivateRouteTeacher>
                        <Report />
                    </PrivateRouteTeacher>
                } />

                <Route path="/book_suggestion" element={
                    <PrivateRoute>
                        <BookSuggestion />
                    </PrivateRoute>
                } />

                <Route render={() => <Navigate to={{ pathname: "/" }} />} />
            </Routes>
        </Router>
    </Provider>


);

export default App