import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { GrTechnology } from 'react-icons/gr'
import { GiEgyptianProfile } from 'react-icons/gi'
import PopupBook from './PopupBook'
import { Modal, Button } from 'react-bootstrap'

function BookSuggestion() {

    const [dataFiction, setDataFiction] = useState([])
    const [dataNonFiction, setDataNonFiction] = useState([])
    const [catChosen, setCatChosen] = useState('All')
    const [idGet, setIdGet] = useState('')
    const [modal, setModal] = useState(false)
    const [loading, setLoading] = useState(false)

    useEffect(async () => {
        setLoading(true)
        axios.get(`https://www.googleapis.com/books/v1/volumes?q=subject:fiction&key=AIzaSyClFXlDcE8c_PNMs3b_UPYzDrqPbDXSlcA`)
            .then((response) => {
                setDataFiction(response.data.items)
                setLoading(false)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(async () => {
        setLoading(true)
        axios.get(`https://www.googleapis.com/books/v1/volumes?q=subject:history&key=AIzaSyClFXlDcE8c_PNMs3b_UPYzDrqPbDXSlcA`)
            .then((response) => {
                setDataNonFiction(response.data.items)
                setLoading(false)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    const scrappedDataFiction = dataFiction.map(x => x.volumeInfo)
    const scrappedDataNonFiction = dataNonFiction.map(x => x.volumeInfo)

    const showPopup = e => {
        setModal(true)
        setIdGet(e.target.id)
    }

    const handleClose = () => setModal(false);


    const catChoose = e => {
        e.preventDefault();
        setCatChosen(e.target.id)
    }

    const spliId = idGet.split('_');
    const test = scrappedDataFiction.filter(x => x.title === spliId[1])

    console.log(spliId[0])
    console.log(test)
    console.log(dataFiction)

    return (
        <div>
            {
                loading ? (
                    <div className="text-center loading-screen">
                        <div className='spinner-main-custom' >
                            <div className="spinner-border" style={{ width: "5rem", height: "5rem" }} role="status">
                                <span className="sr-only"></span>
                            </div>
                        </div>
                    </div>
                ) : null
            }

            <Modal show={modal} onHide={handleClose} aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header>
                    <Modal.Title>
                        {
                            spliId[0] === 'fiction' ? (
                                scrappedDataFiction.filter(x => x.title === spliId[1]).map((x) => {
                                    return (
                                        <p className='text-center'>{x.title}</p>
                                    )
                                })
                            ) : spliId[0] === 'non-fiction' ? (
                                scrappedDataNonFiction.filter(x => x.title === spliId[1]).map((x) => {
                                    return (
                                        <p className='text-center'>{x.title}</p>
                                    )
                                })
                            ) : null
                        }
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {
                        spliId[0] === 'fiction' ? (
                            scrappedDataFiction.filter(x => x.title === spliId[1]).map((x) => {
                                return (
                                    <div>
                                        <img className='d-flex justify-content-center my-3' src={x.imageLinks.thumbnail.replace("http", "https")} alt="" srcset="" />
                                        <p>Penulis: {x.authors}</p>
                                        <p>Penerbit: {x.publisher}</p>
                                        <p>Bahasa: {x.language}</p>
                                        <p className='text-center'>Ringkasan</p>
                                        <p>{x.description}</p>
                                    </div>
                                )
                            })
                        ) : spliId[0] === 'non-fiction' ? (
                            scrappedDataNonFiction.filter(x => x.title === spliId[1]).map((x) => {
                                return (
                                    <div>
                                        <img className='d-flex justify-content-center my-3' src={x.imageLinks.thumbnail.replace("http", "https")} alt="" srcset="" />
                                        <p>Penulis: {x.authors}</p>
                                        <p>Penerbit: {x.publisher}</p>
                                        <p>Bahasa: {x.language}</p>
                                        <p className='text-center'>Ringkasan</p>
                                        <p>{x.description}</p>
                                    </div>
                                )
                            })
                        ) : null
                    }
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>

            <div className='container-fluid container-content'>

                <div className='container-fluid'>
                    <h1 className='suggest-title'>Cadangan Buku</h1>
                    <h4>Sila pilih kategori yang disenaraikan di bawah</h4>
                </div>
                <div className='row justify-content-center my-3'>
                    <div className='col-2'>
                        <div className={catChosen === 'All' ? 'cat-list text-center active-cat' : 'cat-list text-center'} id="All" onClick={(e) => catChoose(e)}>
                            <GrTechnology id="All" />
                            <p id="All">Semua</p>
                        </div>
                    </div>
                    <div className='col-2'>
                        <div className={catChosen === 'fiction' ? 'cat-list text-center active-cat' : 'cat-list text-center'} id="fiction" onClick={(e) => catChoose(e)}>
                            <GrTechnology id="fiction" />
                            <p id="fiction">Fiksyen</p>
                        </div>
                    </div>
                    <div className='col-2' value="">
                        <div className={catChosen === 'non-fiction' ? 'cat-list text-center active-cat' : 'cat-list text-center'} id="non-fiction" onClick={(e) => catChoose(e)}>
                            <GrTechnology id="non-fiction" />
                            <p id="non-fiction">Bukan Fiksyen</p>
                        </div>
                    </div>
                </div>

                {
                    catChosen === 'fiction' ? (
                        <div>
                            <div className='my-3'>
                                <h4>Fiksyen</h4>
                            </div>

                            <div className='row my-5'>
                                {
                                    scrappedDataFiction.map((x, index) => {
                                        return (
                                            <div className='col-2 p-3'>
                                                <div className='card book-suggestion-list p-3 text-center h-100'>
                                                    <div className='card-body' id={`fiction_${x.title}`} onClick={(e) => showPopup(e)}>
                                                        <p id={`fiction_${x.title}`}>{x.title}</p>
                                                        <img id={`fiction_${x.title}`} src={x.imageLinks.smallThumbnail.replace("http", "https")} alt="" srcset="" />
                                                        <div id={`fiction_${x.title}`} className='my-2'>
                                                            {
                                                                x.authors.length > 1 ? (
                                                                    <p id={`fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors[0]}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                ) : (
                                                                    <p id={`fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                )
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    ) : catChosen === 'non-fiction' ? (
                        <div>
                            <div className='my-3'>
                                <h4>Bukan Fiksyen</h4>
                            </div>

                            <div className='row my-5'>
                                {
                                    scrappedDataNonFiction.map((x) => {
                                        return (
                                            <div className='col-2 p-3'>
                                                <div className='card book-suggestion-list p-3 text-center h-100'>
                                                    <div className='card-body' id={`non-fiction_${x.title}`} onClick={(e) => showPopup(e)}>
                                                        <p id={`non-fiction_${x.title}`}>{x.title}</p>
                                                        <img id={`non-fiction_${x.title}`} src={x.imageLinks.smallThumbnail.replace("http", "https")} alt="" srcset="" />
                                                        <div id={`non-fiction_${x.title}`} className='my-2'>
                                                            {
                                                                x.authors.length > 1 ? (
                                                                    <p id={`non-fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors[0]}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                ) : (
                                                                    <p id={`non-fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                )
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    ) : (

                        <div>
                            <div className='my-3'>
                                <h4>Fiksyen</h4>
                            </div>

                            <div className='row my-5'>
                                {
                                    scrappedDataFiction.map((x, index) => {
                                        return (
                                            <div className='col-2 p-3'>
                                                <div className='card book-suggestion-list p-3 text-center h-100'>
                                                    <div className='card-body' id={`fiction_${x.title}`} onClick={(e) => showPopup(e)}>
                                                        <p id={`fiction_${x.title}`}>{x.title}</p>
                                                        <img id={`fiction_${x.title}`} src={x.imageLinks.smallThumbnail.replace("http", "https")} alt="" srcset="" />
                                                        <div id={`fiction_${x.title}`} className='my-2'>
                                                            {
                                                                x.authors.length > 1 ? (
                                                                    <p id={`fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors[0]}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                ) : (
                                                                    <p id={`fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                )
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>

                            <div className='my-3'>
                                <h4>Bukan Fiksyen</h4>
                            </div>

                            <div className='row my-5'>
                                {
                                    scrappedDataNonFiction.map((x) => {
                                        return (
                                            <div className='col-2 p-3'>
                                                <div className='card book-suggestion-list p-3 text-center h-100'>
                                                    <div className='card-body' id={`non-fiction_${x.title}`} onClick={(e) => showPopup(e)}>
                                                        <p id={`non-fiction_${x.title}`}>{x.title}</p>
                                                        <img id={`non-fiction_${x.title}`} src={x.imageLinks.smallThumbnail.replace("http", "https")} alt="" srcset="" />
                                                        <div id={`non-fiction_${x.title}`} className='my-2'>
                                                            {
                                                                x.authors.length > 1 ? (
                                                                    <p id={`non-fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors[0]}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                ) : (
                                                                    <p id={`non-fiction_${x.title}`}>Penulis:<br />
                                                                        {x.authors}<br />
                                                                        Tarikh Diterbitkan: {x.publishedDate}</p>
                                                                )
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    )
}

export default BookSuggestion