import React, { useState } from 'react'
import { Navigate, Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { login } from '../actions/auth'
import CSRFToken from '../components/CSRFToken'
import { ToastContainer } from 'react-toastify'

function Login({ login, isAuthenticated, isRole }) {

  const [formData, setFormData] = useState({
    email: '',
    password: '',
  })


  const { email, password } = formData

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

  const onSubmit = e => {
    e.preventDefault();
    login(email, password);
  }

  if (isAuthenticated && isRole === "Student")
    return <Navigate to="/profile" />
  else if (isAuthenticated && isRole === "Teacher")
    return <Navigate to="/profile_teacher" />


  return (
    <div className='login-main'>
      <div className='login-container container-fluid'>
        <h1 className='login-title-main'>Log masuk akaun anda</h1>
        <h2 className='login-title'>eNilam</h2>
        <form onSubmit={e => onSubmit(e)}>
          <CSRFToken />
          <div className='form-group'>
            <label className='form-label mt-3'>Emel:</label>
            <input className='form-control login-form' type='email' placeholder='Emel*' name='email' onChange={e => onChange(e)} value={email} required />
          </div>
          <div className='form-group'>
            <label className='form-label mt-3'>Kata Laluan:</label>
            <input className='form-control login-form' type='password' placeholder='Kata Laluan*' name='password' onChange={e => onChange(e)} value={password} minLength='6' required />
          </div>
          <button className='btn btn-primary mt-3 login-button' type='submit'>Log Masuk</button>
        </form>
        <div className='d-flex justify-content-around' > 
        <p className='mt-3'>Tiada Akaun? <Link to='/register' className='login-Register'>Daftar</Link></p>
        <p className='mt-3'>Lupa Kata laluan?</p>
        </div>
        
        <ToastContainer
          position="top-center"
          autoClose={2000}
          hideProgressBar={true}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
      </div>
      {/* <div class="ocean">
        <div class="wave"></div>
        <div class="wave"></div>
        <div class="wave"></div>
      </div> */}
      {/* <div class="wave"></div> */}
    </div>
  )
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated,
  isRole: state.profile.role
})

export default connect(mapStateToProps, { login })(Login)