import React, { useState, useEffect, useRef } from 'react'
import { CSVLink } from 'react-csv'
import axios from 'axios';
import { connect } from 'react-redux'
import moment from 'moment';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { Table } from 'react-bootstrap'

function Report({
    user_global,
    kelas_global,
    darjah_global
}) {

    const headers = [
        { label: "Student ID", key: "student_id" },
        { label: "First Name", key: "first_name" },
        { label: "Last Name", key: "last_name" },
        { label: "Darjah", key: "darjah" },
        { label: "Kelas", key: "kelas" },
        { label: "Book Title", key: "book_title" },
        { label: "Date Read", key: "date_read" }
    ];

    const todayDate = new Date()
    const dateForFile = moment(new Date()).format("DD-MM-YYYY");

    const tableRef = useRef(null)

    const [bookList, setBookList] = useState([])
    const [allStudents, setAllStudents] = useState([])
    const [searchDate, setSearchDate] = useState(todayDate)
    const [darjah, setDarjah] = useState([])
    const [kelas, setKelas] = useState([])
    const [selectedDarjah, setSelectedDarjah] = useState('All')

    var months = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"];

    useEffect(async () => {
        axios.get(`${process.env.REACT_APP_API_URL}/books/book/`)
            .then((response) => {
                setBookList(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(async () => {
        axios.get(`${process.env.REACT_APP_API_URL}/profile/all_user/`)
            .then((response) => {
                setAllStudents(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/accounts/darjah/`)
            .then((response) => {
                setDarjah(response.data)
            })
            .catch(error => console.error(`Error: ${error}`))
    }, [])

    // useEffect(async () => {
    //     await axios.get(`${process.env.REACT_APP_API_URL}/accounts/kelas/`)
    //       .then((response) => {
    //         setKelas(response.data)
    //       })
    //       .catch(error => console.error(`Error: ${error}`))
    //   }, [])

    const getMonth = moment(searchDate).month() + 1
    const getYear = moment(searchDate).year()

    function studentFilter(kelas_global, darjah_global) {
        if (kelas_global.includes(",") === true && darjah_global.includes(",") === true) {
            const splitKelas = kelas_global.split(",")
            const splitDarjah = darjah_global.split(",")
            const studentFilter = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).map(x => x.id)
            return studentFilter
        } else if (kelas_global.includes(",") === true && darjah_global.includes(",") === false) {
            const splitKelas = kelas_global.split(",")
            const studentFilter = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah === darjah_global).map(x => x.id)
            return studentFilter
        } else if (kelas_global.includes(",") === false && darjah_global.includes(",") === true) {
            const splitDarjah = darjah_global.split(",")
            const studentFilter = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).map(x => x.id)
            return studentFilter
        } else {
            const studentFilter = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global).map(x => x.id)
            return studentFilter
        }
    }

    function studentFilter2(kelas_global, darjah_global) {
        if (kelas_global.includes(",") === true && darjah_global.includes(",") === true) {
            const splitKelas = kelas_global.split(",")
            const splitDarjah = darjah_global.split(",")
            const studentFilter2 = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).sort((a, b) => {
                return a.id - b.id
            })
            return studentFilter2
        } else if (kelas_global.includes(",") === true && darjah_global.includes(",") === false) {
            const splitKelas = kelas_global.split(",")
            const studentFilter2 = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah === darjah_global).sort((a, b) => {
                return a.id - b.id
            })
            return studentFilter2
        } else if (kelas_global.includes(",") === false && darjah_global.includes(",") === true) {
            const splitDarjah = darjah_global.split(",")
            const studentFilter2 = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).sort((a, b) => {
                return a.id - b.id
            })
            return studentFilter2
        } else {
            const studentFilter2 = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global).sort((a, b) => {
                return a.id - b.id
            })
            return studentFilter2
        }
    }

    const result = bookList.filter(x => studentFilter(kelas_global, darjah_global).includes(x.created_by)).sort((a, b) => {
        return a.id - b.id
    })

    const filterByDate = result.filter(x => moment(x.date_read).month() + 1 === getMonth && moment(x.date_read).year() === getYear)
    const darjahValue = darjah.map(x => x.value)

    function handleSelectedDarjah(event) {
        setSelectedDarjah(event.target.value)
    }

    // const kelasTest = kelas_global.includes(",")
    const darjahTest = darjah_global.includes(",")
    const darjahGlobal = darjah_global

    const newData = filterByDate.map((x) => (
        {
            student_id: `${studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.id)}`,
            first_name: `${studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.first_name)}`,
            last_name: `${studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.last_name)}`,
            darjah: `${studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.darjah)}`,
            kelas: `${studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.kelas)}`,
            book_title: x.book_title,
            date_read: x.date_read
        }
    ))

    const filterByDarjah = newData.filter(x => x.darjah === selectedDarjah)

    console.log(selectedDarjah)

    return (
        <div className='container-fluid container-content'>
            <h1 className='report-title'>Laporan</h1>
            <h6 className="text-left mb-3 mt-3">Tapis Mengikut:</h6>
            <div className='d-flex justify-content-around my-3'>
                <div>
                    <label className='form-label' htmlFor='month_year'>Bulan/Tahun: </label>
                    <DatePicker
                        selected={searchDate}
                        onChange={setSearchDate}
                        dateFormat="MM/yyyy"
                        showMonthYearPicker
                        showFullMonthYearPicker
                        className="form-control"
                    />
                </div>
                <div>
                    <label className='form-label' htmlFor='darjah'>Darjah: </label>
                    <select className="form-control" value={selectedDarjah} onChange={handleSelectedDarjah}>
                        <option value="All">Semua</option>
                        {
                            darjahTest === true ? (
                                darjah.filter(x => x.key >= darjahGlobal.split(",")[0] && x.key <= darjahGlobal.split(",")[darjahGlobal.split(",").length - 1]).map((x) => {
                                    return (
                                        <option className='form-control' value={x.key}>{x.value}</option>
                                    )
                                })
                            ) : (
                                darjah.filter(x => x.key === darjahGlobal).map((x) => {
                                    return (
                                        <option className='form-control' value={x.key}>{x.value}</option>
                                    )
                                })
                            )
                        }
                    </select>
                </div>
                <div className='button-export'>
                    {
                        selectedDarjah === "All" ? (
                            <CSVLink
                                headers={headers}
                                data={newData}
                                filename={`${dateForFile}` + "_" + `${selectedDarjah}` + "_result.csv"}
                                target="_blank"
                                className='btn btn-primary'
                            >
                                Export
                            </CSVLink>
                        ) : (
                            <CSVLink
                                headers={headers}
                                data={filterByDarjah}
                                filename= {`${dateForFile}` + "_" + `${selectedDarjah}` + "_result.csv"}
                                target="_blank"
                                className='btn btn-primary'
                            >
                                Export
                            </CSVLink>
                        )
                    }
                </div>
            </div>
            <div className='container-fluid'>
                <Table ref={tableRef}>
                    <thead>
                        <tr>
                            <th scope="col">Nama</th>
                            <th scope="col">Darjah</th>
                            <th scope="col">Kelas</th>
                            <th scope="col">Tajuk Buku</th>
                            <th scope="col">Tarikh Hantar</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            selectedDarjah === 'All' ? (
                                newData.map((x) => {
                                    return (
                                        <tr>
                                            <td>{x.first_name} {x.last_name}</td>
                                            <td>{x.darjah}</td>
                                            <td>{x.kelas}</td>
                                            <td>{x.book_title}</td>
                                            <td>{x.date_read}</td>
                                        </tr>
                                    )
                                })
                            ) : (
                                filterByDarjah.map((x) => {
                                    return (
                                        <tr>
                                            <td>{x.first_name} {x.last_name}</td>
                                            <td>{x.darjah}</td>
                                            <td>{x.kelas}</td>
                                            <td>{x.book_title}</td>
                                            <td>{x.date_read}</td>
                                        </tr>
                                    )
                                })
                            )
                        }
                    </tbody>
                </Table>
            </div>
        </div>
    )
}

const mapStateToProps = state => ({
    user_global: state.profile.user,
    kelas_global: state.profile.kelas,
    darjah_global: state.profile.darjah
})


export default connect(mapStateToProps, {})(Report)