import Cookies from 'js-cookie'
import axios from 'axios'
import {
    INPUT_BOOK_DATA_SUCCESS,
    INPUT_BOOK_DATA_FAIL,
    UPDATE_BOOK_DATA_SUCCESS,
    UPDATE_BOOK_DATA_FAIL,
    UPDATE_BOOK_TEACHER_DATA_SUCCESS,
    UPDATE_BOOK_TEACHER_DATA_FAIL,
    APPROVE_BOOK_TEACHER_DATA_SUCCESS,
    APPROVE_BOOK_TEACHER_DATA_FAIL
} from './types'
import { toast } from 'react-toastify'

export const input_book = (book_title, book_author, date_published, date_read, page, language, tags, summary) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({
        'withCredentials': true,
        book_title, book_author, date_published, date_read, page, language, tags, summary
    })

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/books/book/`, body, config);

        if (res.data.error) {
            dispatch({
                type: INPUT_BOOK_DATA_FAIL,
            });
            toast.error(res.data.error, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else if (res.data.success) {
            dispatch({
                type: INPUT_BOOK_DATA_SUCCESS,
                payload: res.data
            });
            toast.success(res.data.success, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }

    } catch (err) {
        dispatch({
            type: INPUT_BOOK_DATA_FAIL
        })
    }
}

export const update_book = (
    id,
    book_title,
    book_author,
    date_published,
    date_read,
    page,
    language,
    tags,
    summary,
    comment) => async dispatch => {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'X-CSRFToken': Cookies.get('csrftoken')
            }
        }

        const body = JSON.stringify({
            'withCredentials': true,
            id,
            book_title,
            book_author,
            date_published,
            date_read,
            page,
            language,
            tags,
            summary,
            comment
        })

        try {
            const res = await axios.put(`${process.env.REACT_APP_API_URL}/books/book/id=${id}/`, body, config);

            if (res.data.success) {
                dispatch({
                    type: UPDATE_BOOK_DATA_SUCCESS,
                    payload: res.data
                });
                toast.success(res.data.success, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            } else if (res.data.error) {
                dispatch({
                    type: UPDATE_BOOK_DATA_FAIL,
                });
                toast.error(res.data.error, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }

        } catch (err) {
            dispatch({
                type: UPDATE_BOOK_DATA_FAIL
            })
        }
    }

