import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import { Table } from 'react-bootstrap'
import { Link } from 'react-router-dom'

function BookListTeacher({
  user_global,
  kelas_global,
  darjah_global
}) {

  const [role, setRole] = useState([])
  const [darjah, setDarjah] = useState([])
  const [kelas, setKelas] = useState([])

  const [bookList, setBookList] = useState([])
  const [allStudents, setAllStudents] = useState([])

  useEffect(async () => {
    axios.get(`${process.env.REACT_APP_API_URL}/books/book/`)
      .then((response) => {
        setBookList(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(async () => {
    axios.get(`${process.env.REACT_APP_API_URL}/profile/all_user/`)
      .then((response) => {
        setAllStudents(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/role/`)
      .then((response) => {
        setRole(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/darjah/`)
      .then((response) => {
        setDarjah(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  useEffect(() => {
    axios.get(`${process.env.REACT_APP_API_URL}/accounts/kelas/`)
      .then((response) => {
        setKelas(response.data)
      })
      .catch(error => console.error(`Error: ${error}`))
  }, [])

  const booksFilteredByUser = bookList.filter(x => x.created_by === user_global)


  function studentFilter(kelas_global, darjah_global) {
    if (kelas_global.includes(",") === true && darjah_global.includes(",") === true) {
      const splitKelas = kelas_global.split(",")
      const splitDarjah = darjah_global.split(",")
      const studentFilter = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).map(x => x.id)
      return studentFilter
    } else if (kelas_global.includes(",") === true && darjah_global.includes(",") === false){
      const splitKelas = kelas_global.split(",")
      const studentFilter = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah === darjah_global).map(x => x.id)
      return studentFilter
    } else if(kelas_global.includes(",") === false && darjah_global.includes(",") === true){
      const splitDarjah = darjah_global.split(",")
      const studentFilter = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1]).map(x => x.id)
      return studentFilter
    } else {
      const studentFilter = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global).map(x => x.id)
      return studentFilter
    }
  }

  function studentFilter2(kelas_global, darjah_global) {
    if (kelas_global.includes(",") === true && darjah_global.includes(",") === true) {
      const splitKelas = kelas_global.split(",")
      const splitDarjah = darjah_global.split(",")
      const studentFilter2 = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1])
      return studentFilter2
    } else if (kelas_global.includes(",") === true && darjah_global.includes(",") === false){
      const splitKelas = kelas_global.split(",")
      const studentFilter2 = allStudents.filter(x => x.kelas >= splitKelas[0] && x.kelas <= splitKelas[splitKelas.length - 1] && x.role === "Student" && x.darjah === darjah_global)
      return studentFilter2
    } else if(kelas_global.includes(",") === false && darjah_global.includes(",") === true){
      const splitDarjah = darjah_global.split(",")
      const studentFilter2 = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah >= splitDarjah[0] && x.darjah <= splitDarjah[splitDarjah.length - 1])
      return studentFilter2
    } else{
      const studentFilter2 = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global)
      return studentFilter2
    }
  }


  // const studentFilter = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global).map(x => x.id)
  // const studentFilter2 = allStudents.filter(x => x.kelas === kelas_global && x.role === "Student" && x.darjah === darjah_global)
  const darjahValue = darjah.map(x => x.value)
  const kelasValue = kelas.map(x => x.value)

  const result = bookList.filter(x => studentFilter(kelas_global, darjah_global).includes(x.created_by)).sort((a, b) => {
    return a.id - b.id
  })

  console.log(result)
  console.log(studentFilter2(kelas_global, darjah_global))
  console.log(kelas_global)
  console.log(darjah_global)

  return (
    <div className='container-fluid container-content'>
      <div className='container-fluid'>
        <Table>
          <thead>
            <tr>
              <th scope="col">Nama</th>
              <th scope="col">Darjah</th>
              <th scope="col">Kelas</th>
              <th scope="col">Tajuk Buku</th>
              <th scope="col">Tarikh Hantar</th>
              <th scope="col">Status</th>
            </tr>
          </thead>
          <tbody>
            {
              result.map((x) => {
                return (
                  <tr>
                    <td>{studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.first_name)} {studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.last_name)}</td>
                    <td>{studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => darjahValue[y.darjah - 1])}</td>
                    <td>{studentFilter2(kelas_global, darjah_global).filter(y => y.id === x.created_by).map(y => y.kelas)}</td>
                    <td>{x.book_title}</td>
                    <td>{x.date_read}</td>
                    {
                      x.approval_status === "Comment Added" ? 
                        <td className='text-danger'>Komen Ditambah</td>
                      : x.approval_status === "Pending Approval" ? 
                        <td className='text-warning'>Menunggu Kelulusan</td>
                      : x.approval_status === "Approved" ? 
                        <td className='text-success'>Diluluskan</td>
                      : null 
                    }
                    {/* <td className={x.approval_status === "Comment Added" ? "text-danger" : x.approval_status === "Pending Approval" ? "text-warning" : x.approval_status === "Approved" ? "text-success" : ""}>{x.approval_status}</td> */}
                    <td><Link className='btn btn-primary' to={`/update_book_teacher/${x.uid}`}>Lihat butiran</Link></td>
                  </tr>
                )
              })
            }
          </tbody>
        </Table>
      </div>
    </div>
  )
}

const mapStateToProps = state => ({
  user_global: state.profile.user,
  kelas_global: state.profile.kelas,
  darjah_global: state.profile.darjah
})

export default connect(mapStateToProps, {})(BookListTeacher)