import {
    INPUT_BOOK_DATA_SUCCESS,
    INPUT_BOOK_DATA_FAIL,
    UPDATE_BOOK_DATA_SUCCESS,
    UPDATE_BOOK_DATA_FAIL
} from '../actions/types'

const initialState = {
    book_title: '',
    book_author: '',
    date_published: '', 
    date_read: '', 
    page: '', 
    language: '', 
    tags: '', 
    summary: '',}

export default function (state = initialState, action) {
    const { type, payload } = action

    switch (type) {
        case INPUT_BOOK_DATA_SUCCESS:
        case UPDATE_BOOK_DATA_SUCCESS:
            return {
                ...state,
                book_title: payload.book_title, 
                book_author: payload.book_author, 
                date_published: payload.date_published, 
                date_read: payload.date_read, 
                page: payload.page, 
                language: payload.language, 
                tags: payload.tags, 
                summary: payload.summary,
            }
        case INPUT_BOOK_DATA_FAIL:
        case UPDATE_BOOK_DATA_FAIL:
            return {
                ...state,
                book_title: '', 
                book_author: '', 
                date_published: '', 
                date_read: '', 
                page: '', 
                language: '', 
                tags: '', 
                summary: '',
            }
        default:
            return state
    }
}

