import Cookies from 'js-cookie'
import axios from 'axios'
import {
    LOAD_USER_PROFLE_FAIL,
    LOAD_USER_PROFLE_SUCCESS,
    UPDATE_USER_PROFLE_SUCCESS,
    UPDATE_USER_PROFLE_FAIL,
} from './types'
import { toast } from 'react-toastify'

export const load_user = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    }

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/profile/user/`, config);

        if (res.data.error) {
            dispatch({
                type: LOAD_USER_PROFLE_FAIL,
            });
        } else {
            dispatch({
                type: LOAD_USER_PROFLE_SUCCESS,
                payload: res.data
            });
        }

    } catch (err) {

    }
}

export const update_profile = (first_name, last_name, role, darjah, kelas) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({
        'withCredentials': true,
        first_name,
        last_name,
        role,
        darjah,
        kelas
    })

    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/profile/update_user/`, body, config)

        if (res.data.profile && res.data.email) {
            dispatch({
                type: UPDATE_USER_PROFLE_SUCCESS,
                payload: res.data
            })
            toast.success(res.data.success, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } else {
            dispatch({
                type: UPDATE_USER_PROFLE_FAIL
            })
            toast.error(res.data.error, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    } catch (err) {
        dispatch({
            type: UPDATE_USER_PROFLE_FAIL
        })
    }
}